  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1230px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  margin-bottom: 5px;
  padding: 10px 25px;
  font-family: "Gilroy-Medium", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 23px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background: linear-gradient(274.27deg, rgba(255, 255, 255, 0) -14.6%, rgba(255, 255, 255, 0.39) 39.52%, rgba(255, 255, 255, 0) 110.6%), linear-gradient(263.81deg, #FFC771 10.32%, #FFC858 98.17%);
  box-shadow: inset 0px 2px 15px rgba(255, 255, 255, 0.41), inset 19px 0px 25px rgba(255, 255, 255, 0.11);
  border-radius: 10px;
  position: relative;
}
.button::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(131, 93, 19, 0.36), rgba(131, 93, 19, 0.36)), linear-gradient(263.81deg, #FBD293 10.32%, #FFD172 98.17%);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 10px;
  top: 5px;
  left: 0;
  z-index: -1;
}
.button:hover {
  top: 5px;
}
.button:hover::after {
  top: 0;
}
@media screen and (max-width: 576px) {
  .button {
    font-size: 10px;
    line-height: 19px;
  }
}

