.modal {
  position: fixed;
  left: 0;
  top: 0;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
}
.modal__body {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
  min-height: 100%;
  transition: all 0.8s ease 0s;
}
.modal__content {
  position: relative;
  padding: 30px 45px 55px 30px;
  max-width: 400px;
  background-color: white;
  opacity: 0;
