.hero
  position: relative
  padding-top: 140px
  padding-bottom: 145px
  background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url(../../img/hero/hero-background.jpg)
  background-repeat: no-repeat
  background-size: cover
  background-position: center
  @media screen and ( max-width: 768px )
    padding-top: 110px
    padding-bottom: 115px
  @media screen and ( max-width: 576px )
    padding-top: 100px
    padding-bottom: 105px

  &__wrap
    display: flex
    flex-direction: column
    align-items: flex-start
    position: relative
    z-index: 2

  &__title
    margin: 0
    margin-bottom: 10px
    @media screen and ( max-width: 768px )
      margin-bottom: 0

  &__subtitle
    margin: 0
    margin-bottom: 65px
    font-family: "Gilroy-Bold", sans-serif
    font-weight: 700
    color: $macaroni-cheese
    position: relative
    &::after
      position: absolute
      content: ""
      width: 100px
      border-top: 3px solid $macaroni-cheese
      top: 86px
      left: 0
    @media screen and ( max-width: 768px )
      margin-bottom: 45px
      &::after
        top: 76px
    @media screen and ( max-width: 576px )
      margin-bottom: 25px
      &::after
        top: 50px

  &__text
    margin: 0
    margin-bottom: 40px
    font-family: "Gilroy-Regular", sans-serif
    font-weight: 400
    font-size: 25px
    line-height: 30px
    @media screen and ( max-width: 768px )
      margin-bottom: 20px
    @media screen and ( max-width: 576px )
      font-size: 19px
      line-height: 23px

  &__button
    padding: 30px 60px
    font-family: "Gilroy-Bold", sans-serif
    font-weight: 700
    color: $raw-umber
    &::before
      position: absolute
      content: ""
      width: 140px
      height: 140px
      background-image: url(../../img/hero/hero-decoration.png)
      background-repeat: no-repeat
      background-size: cover
      background-position: center
      top: -33px
      right: -79px
    &:hover
      &::before
        top: -38px
    @media screen and ( max-width: 768px )
      padding: 20px 40px
      &::before
        width: 110px
        height: 110px
        top: -26px
        right: -61px
      &:hover
        &::before
          top: -31px
    @media screen and ( max-width: 576px )
      padding: 10px 20px
      &::before
        width: 80px
        height: 80px
        top: -23px
        right: -46px
      &:hover
        &::before
          top: -28px
